@import 'variables';

.wrapper {
  overflow-y: auto;
}

// Adding this padding for laptop screens, because we are sure that the user will
// see a scrollbar below this width
@media screen and (max-width: 1600px) {
  .wrapper {
    padding-right: 20px;
  }
}

@media screen and (max-width: $mobile-breakpoint-max-width) {
  .wrapper {
    padding-right: 0;
  }
}